        <a-button @click="addProductsDatas" type="primary" style="margin-left:8px;margin-right:20px">保存</a-button>
      </a-col>
    </a-row>
    <div class="content_box">
        <a-layout>
            <a-layout-content>
              <div class="dialog_form dialog_formgukuan">
                  <div class="top_form">
                    <tab-warpformInfo ref="WarpFormDatas" :productId="-1"   @changeComponent1Data="component1DataChange" :ProductForm="ProductForm"></tab-warpformInfo>
                  </div>
              </div>
              <div class="dialog_tab">
                  <div  class="dialog_form2 dialog_formgukuan">
                      <a-tabs defaultActiveKey="1" @change="callback">
                          <a-tab-pane tab="常规" key="1">
                              <tab-routineformInfo ref="RoutineFormDatas" :productId="-1" ></tab-routineformInfo>
                          </a-tab-pane>
                          <a-tab-pane tab="物流信息" key="2" class="dialog_tab_table">
                               <tab-packingInfo  @changeCallBack="changePackingDatas" :productId="-1"></tab-packingInfo>
                          </a-tab-pane>
                          <a-tab-pane tab="扩展属性" key="3" class="dialog_tab_table">
                               <a-table :columns="attributescolumns" :dataSource="ProductForm.productAttributesList"  bordered :pagination="showPagination"  :rowKey="(v,i)=>{return i}">
