                          </a-tab-pane>
                          <a-tab-pane tab="技术文档" key="4" class="dialog_tab_table">
                              <tab-documentInfo @changeCallBack="changeDocumentDatas" :productId="-1"></tab-documentInfo>
                          </a-tab-pane>
                          <a-tab-pane tab="图纸" key="5" class="dialog_tab_table">
                              <tab-drawingInfo @changeCallBack="changeDrawingDatas" :productId="-1"></tab-drawingInfo>
                          </a-tab-pane>
                          
