        methods: {
            component1DataChange(params) {
               this.ProductForm = params;
            },
            callback (key) {
                //console.log(key)
            },
        
             //搜集物流信息
            changePackingDatas(addForms){
                this.ProductForm.productPackingList = addForms.productPackingList
                this.ProductForm.delProductPackingList = addForms.delProductPackingList
                console.log("===========新增、修改、删除物流");
                console.log(this.ProductForm);    
            },

            //搜集扩展属性信息
            changeAttributesDatas(addForms){
                this.ProductForm.productAttributesList = addForms.productAttributesList
                this.ProductForm.delProductAttributesList = []
                console.log("===========新增、修改、删除扩展属性");
                console.log(this.ProductForm);    
            },

            //搜集技术文档信息
            changeDocumentDatas(addForms){
                console.log(addForms);    
                this.ProductForm.productDocumentationList = addForms.productDocumentationList
                this.ProductForm.delProductDocumentationList = []
                console.log("===========新增、修改、删除技术文档");
                console.log(this.ProductForm);    
            },

            //搜集图纸信息
            changeDrawingDatas(addForms){
                console.log(addForms);    
                this.ProductForm.productDrawingList = addForms.productDrawingList
                this.ProductForm.delProductDrawingList = []
                console.log("===========新增、修改、删除图纸");
                console.log(this.ProductForm);    
            },

            //搜集常规表单信息
            changeRoutineFormDatas(){
                //this.ProductForm.productRoutine = this.$refs.RoutineForm;   
                console.log("===========新增、修改常规表单信息"); 
                this.ProductForm.productRoutine = this.$refs.RoutineFormDatas.productRoutineForm2
                console.log(this.ProductForm.productRoutine); 
            },

            //搜集最外层表单信息
            changeWarpFormDatas(){ 
                console.log("===========新增、修改最外层表单信息"); 
                this.ProductForm.productChart = this.$refs.WarpFormDatas.productWarpForm2.productChart
                this.ProductForm.productCode = this.$refs.WarpFormDatas.productWarpForm2.productCode
                this.ProductForm.productDescribe = this.$refs.WarpFormDatas.productWarpForm2.productDescribe
                this.ProductForm.productMaterialQuality = this.$refs.WarpFormDatas.productWarpForm2.productMaterialQuality
                this.ProductForm.productModel = this.$refs.WarpFormDatas.productWarpForm2.productModel
                this.ProductForm.productName = this.$refs.WarpFormDatas.productWarpForm2.productName
                this.ProductForm.productSpecs = this.$refs.WarpFormDatas.productWarpForm2.productSpecs
                this.ProductForm.claCode = this.$refs.WarpFormDatas.productWarpForm2.claCode
                this.ProductForm.productPicture = this.$refs.WarpFormDatas.productWarpForm2.productPicture
                console.log(this.ProductForm); 

            

            },

            //提交所有收集的数据
            addProductsDatas(e) {
                //收集常规Form  
                this.changeRoutineFormDatas();

                //收集Warp Form  
                this.changeWarpFormDatas()

                //表单数据验证完成之后，提交数据;
                if(this.ProductForm.productName !== "" && this.ProductForm.claCode!== null ){
                    addProduct(this.ProductForm).then((res) => {
                        if (res.data.code === this.$global.successCode) {
                            // console.log("修改零部件成功",res);
                            this.$message.success('添加零部件成功!');
                            this.$emit('getProduct'); //新增产品接口调用成功以后，调用产品list组建里面的获取产品列表的方法刷新产品列表页面的数据
                            this.$emit('close'); //关闭页面
                        }else{
                            this.$message.error(res.data.message);
                        }
                        }, function (error) {
                        console.log("添加零部件失败", error);
                    });  
                }else{
                    this.$message.error("零部件名称或者零部件分类为空");
                }
            }, 

            //修改新增扩展属性节点的每个元素的值
            onCellChange (corRow, dataIndex, value) {
                console.log("dataIndex")
                console.log(dataIndex)
                console.log("value")
                console.log(value)
                console.log("corRow")
                console.log(corRow)
                const data1 = [... this.ProductForm.productAttributesList]
                const target = data1.find(item => item.attName === corRow.attName)

                if (target) {
                    target[dataIndex] = value
                    data1.productCode= this.curproductCode
                    console.log("data1")
                     console.log(data1)
                    this.ProductForm.productAttributesList = data1
                    

                    console.log("this.ProductForm333")
                    console.log(this.ProductForm)
                }
